.login-cat{
  height: 400px;
  border-radius: 112px;
  background: none;
  margin-right: auto;
  /* margin: auto; */

margin-top: auto;}

.login-title {
  /* color: #ff6f61; Cute coral color */
  display: block;
  margin-bottom: 40px;
  font-size: 34px;
  margin-top: 40px;
  font-weight: 600;
}

.login-form {
  display: flex;
  flex-direction: row;
}

.form-group {
  margin-bottom: 20px;
}

.form-label {
  /* display: block; */
  margin-bottom: 8px;
  margin-right: 22px;
  color: #333; /* Dark text color */
  font-weight: 500;
}

.form-input {
  padding: 12px;
  border: 1px solid #e0e0e0; /* Light border color */
  border-radius: 8px; /* Rounded corners */
  box-sizing: border-box; /* Ensure padding does not affect width */
  font-size: 16px;
  color: #555;
}

.form-input::placeholder {
  color: #aaa; /* Placeholder text color */
}

.submit-button {
  width: 100px;
  padding: 12px;
  display: block;
  margin-top: 10px;
  border: none;
  border-radius: 8px;
  background-color: #ff6f61; /* Cute coral color */
  color: #fff; /* White text */
  font-size: 18px;
  margin: auto;
  margin-bottom: 40px;
  margin-top: 40px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #ff5a49; /* Darker coral on hover */
}

.back-button {
  
  margin-top: 10px;
  padding: 12px;
  border: none;
  border-radius: 8px;
  background-color: #f0f0f0; /* Light gray */
  color: #333; /* Dark text */
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #e0e0e0; /* Slightly darker gray on hover */
}

.register-link {
  margin-top: 20px;
  text-align: center;
}

.register-link button {
  background: none;
  border: none;
  color: #ff6f61;
  cursor: pointer;
  font-size: 16px;
  text-decoration: underline;
  transition: color 0.3s ease;
}

.register-link button:hover {
  color: #ff5a49;
}
