.intro{
    background-image: url("../images/pinkback.jpg");
    background-position: center;
    height: 100vh;
    /* background-size: cover; */
    margin: 0px;
    height: 80vh;
    display: flex;
    justify-content: space-around;
}
h1{
    padding: 0px;
    margin: 0px;
}
.intro-image{
    height: 50vh;
}
.intro-quote {
    display: flex;
    flex-direction: column;
    align-items: center; 
    font-size: larger;
    justify-content: center;
    text-align: left; /* Align text to the left */
  }
  
 
  
.intro-quote h2 {
    margin: 0;
    margin-top: 12px;
    display: flex;
    align-self: flex-end;
    font-size: large;
    justify-content: flex-end;
    text-align: right; /* Align the authorâ€™s name to the end */
}

.intro-quote button {
    background-color: #ff6f61; 
    /* background-color: #d73223; */
    color: white;
    border: none;
    padding: 12px 24px; /* Larger padding for better clickability */
    font-size: 2.1rem; /* Slightly larger font */
    cursor: pointer;
    border-radius: 8px; /* Rounded corners for a modern look */
    margin-top: 26px; /* Space between quote and button */
    transition: background-color 0.3s ease, transform 0.3s ease; /* Smooth color and transform transitions */
    align-self: flex-start; /* Align button to the start of .intro-quote */
  }

  .intro-quote button:hover {
    background-color: #d82e22; 
    transform: scale(1.05); 
  }
  
  .intro-quote button:active {
    transform: scale(0.98);
  }